/* ./src/index.css */
@tailwind base;
@tailwind components;
@tailwind utilities;

/* importing slick carousel */
@import "~slick-carousel/slick/slick.css"; 
@import "~slick-carousel/slick/slick-theme.css";
@import url('https://fonts.googleapis.com/css2?family=Open+Sans:ital,wght@0,300;0,400;0,600;0,700;0,800;1,300;1,400;1,600;1,700;1,800&display=swap');



.page-appear {
    opacity: 0;
    transform: translateY(-100%);
}


.page-appear-active {
    opacity: 1;
    transform: translateY(0);
    @apply transition-all duration-300;
}


.page-appear-done {
    opacity: 1;
    transform: translateY(0);
    @apply transition-all duration-300;
}


.page-enter {
    opacity: 0;
    transform: translateY(-100%);
    @apply transition-all duration-300;
}

.page-enter-active {
    opacity: 1;
    transform: translateY(0);
    @apply transition-all duration-300;
}

.page-exit {
    opacity: 1;
    transform: translateY(0);
    @apply transition-all duration-300;
}

.page-exit-active {
    opacity: 0;
    transform: translateY(100%);
    @apply transition-all duration-300;
}

/*reverse animation*/

.rpage-appear {
    opacity: 0;
    transform: translateY(100%);
    @apply transition-all duration-300;
}


.rpage-appear-active {
    opacity: 1;
    transform: translateY(0);
    @apply transition-all duration-300;
}


.rpage-appear-done {
    opacity: 1;
    transform: translateY(0);
    @apply transition-all duration-300;
}


.rpage-enter {
    opacity: 0;
    transform: translateY(100%);
    @apply transition-all duration-300;
}

.rpage-enter-active {
    opacity: 1;
    transform: translateY(0);
    @apply transition-all duration-300;
}

.rpage-exit {
    opacity: 1;
    transform: translateY(0);
    @apply transition-all duration-300;
}

.rpage-exit-active {
    opacity: 0;
    transform: translateY(-100%);
    @apply transition-all duration-300;
}


/* Hide scrollbar for Chrome, Safari and Opera */
.overflow-full-scroller-hidden::-webkit-scrollbar {
    display: none;
}

/* Hide scrollbar for IE, Edge and Firefox */
.overflow-full-scroller-hidden{
    overflow-y: scroll;
    -ms-overflow-style: none;  /* IE and Edge */
    scrollbar-width: none;  /* Firefox */
}

.ReactModal__Overlay{
    z-index: 9999 !important;
}


.fade-enter {
    opacity: 0;
    @apply transition-all duration-300;
}

.fade-enter-active {
    opacity: 1;
    @apply transition-all duration-300;
}

.fade-exit {
    opacity: 1;
    @apply transition-all duration-300;
}

.fade-exit-active {
    opacity: 0;
    @apply transition-all duration-300;
}



.alert-enter {
    opacity: 0;
    transform: scale(0);
}
.alert-enter-active {
    opacity: 1;
    transform: scale(1);
    transition: opacity 300ms, transform 300ms;
}
.alert-exit {
    opacity: 1;
}
.alert-exit-active {
    opacity: 0;
    transform: scale(0);
    transition: opacity 300ms, transform 300ms;
}

